/* src/pages/References.css */

.references-list {
    margin: 20px 0;
  }
  
  .references-list .collapsible-section {
    margin-bottom: 15px;
  }
  
  .references-list a {
    color: #004d99;
    text-decoration: none;
  }
  
  .references-list a:hover {
    text-decoration: underline;
  }
  
  .references-list p {
    margin: 5px 0;
    font-size: 0.95rem;
  }
  