/* src/components/Tooltip.css */

/* Tooltip container */
.tooltip {
    position: relative;
    display: inline-block;
    text-decoration: underline;
    cursor: pointer;
    color: #007bff;
  }
  
  /* Tooltip text styling */
  .tooltiptext {
    visibility: visible;
    width: 220px;
    background-color: rgba(0, 0, 0, 0.85); /* Dark semi-transparent background */
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 8px;
    position: absolute;
    z-index: 1000;
    bottom: 150%; /* Position above the term */
    left: 50%;
    transform: translateX(-50%);
    opacity: 1;
    transition: opacity 0.3s ease;
  }
  
  /* Tooltip arrow */
  .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%; /* Position arrow at bottom of tooltip */
    left: 50%;
    transform: translateX(-50%);
    border-width: 5px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.85) transparent transparent transparent;
  }
  