/* src/components/CollapsibleSection.css */

.collapsible-section {
    margin-top: 20px;
    border-radius: 5px;
  }
  
  /* Ensure title styling is specific to collapsible-section */
  .collapsible-section > .collapsible-title {
    cursor: pointer;
    background-color: #004d99; /* Consistent background color */
    color: #ffffff;
    padding: 12px 15px;
    font-size: 1.2em;
    font-weight: bold;
    border-radius: 5px 5px 0 0;
    margin: 0;
    line-height: 1.2;
    transition: background-color 0.3s ease, color 0.3s ease;
    text-shadow: none; /* Explicitly set text-shadow to none */
  }
  
  .collapsible-section > .collapsible-title:hover {
    background-color: #003366; /* Slightly darker on hover */
    color: #f0f8ff; /* Softer contrast on hover */
  }
  
  .collapsible-section > .collapsible-content {
    padding: 15px;
    background-color: #f1f4f8;
    color: #333;
    border: 1px solid #004d99;
    border-top: none;
    border-radius: 0 0 5px 5px;
  }
  
  /* Optional additional styling for mobile */
  @media (max-width: 768px) {
    .collapsible-section > .collapsible-title {
      font-size: 1em;
      padding: 10px;
    }
  
    .collapsible-section > .collapsible-content {
      padding: 10px;
    }
  }
  