/* src/components/Navigation.css */

nav {
    background-color: #333;
    padding: 15px;
  }
  
  nav ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style-type: none;
    padding: 0;
    margin: 0;
    gap: 15px;
  }
  
  nav ul li {
    margin: 0;
  }
  
  nav ul li a {
    color: #ffffff;
    text-decoration: none;
    font-weight: bold;
    padding: 10px 15px;
    transition: color 0.3s ease;
  }
  
  nav ul li a.active,
  nav ul li a:hover {
    color: #ffcccb;
  }
  
  /* Responsive Design for Smaller Screens */
  @media (max-width: 768px) {
    nav ul {
      flex-direction: column;
      align-items: center;
    }
  
    nav ul li a {
      padding: 10px;
      width: 100%;
      text-align: center;
    }
  }
  