/* src/pages/Quiz.css */

.quiz-container {
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .quiz-question {
    font-size: 1.2em;
    font-weight: bold;
    color: #333;
    margin-bottom: 15px;
  }
  
  .quiz-options {
    list-style-type: none;
    padding: 0;
    margin-top: 10px;
  }
  
  .quiz-option-btn {
    width: 100%;
    padding: 10px;
    margin: 5px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
    cursor: pointer;
    transition: background-color 0.2s ease, color 0.2s ease;
  }
  
  .quiz-option-btn:hover {
    background-color: #f0f0f0;
  }
  
  .quiz-option-btn.selected {
    background-color: #007bff;
    color: #fff;
    border-color: #007bff;
  }
  
  .quiz-navigation {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .quiz-navigation button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease;
  }
  
  .quiz-navigation button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  .quiz-navigation button:hover:not(:disabled) {
    background-color: #0056b3;
  }
  
  .quiz-score-section {
    text-align: center;
    font-size: 1.2em;
    color: #28a745;
    margin-top: 20px;
  }
  
  .quiz-restart-btn {
    background-color: #ffc107;
    color: #333;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease;
    margin-top: 20px;
  }
  
  .quiz-restart-btn:hover {
    background-color: #e0a800;
  }
  