/* src/pages/Glossary.css */

/* Glossary Container */
.glossary-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
  }
  
  /* Glossary Item Styling */
  .glossary-item {
    padding: 15px;
    background-color: #f5f5f5;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .glossary-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }
  
  /* Term Styling */
  .glossary-term {
    font-size: 1.3em;
    font-weight: bold;
    color: #007bff;
    margin-bottom: 5px;
    position: relative;
  }
  
  /* Underline effect */
  .glossary-term::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -3px;
    height: 2px;
    width: 0;
    background-color: #007bff;
    transition: width 0.3s ease;
  }
  
  .glossary-term:hover::after {
    width: 100%;
  }
  
  /* Definition Styling */
  .glossary-definition {
    font-size: 1em;
    color: #555;
    margin-top: 8px;
    line-height: 1.6;
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .glossary-container {
      padding: 10px;
    }
  
    .glossary-item {
      padding: 10px;
    }
  
    .glossary-term {
      font-size: 1.2em;
    }
  
    .glossary-definition {
      font-size: 0.95em;
    }
  }
  